
import { defineComponent } from 'vue'
import HtsTemplate from "@/apps/HTS/views/reports/templates/DataTableTemplate.vue";
import { ReportTemplateParams } from '@/apps/HTS/types/ReportTypes'
import { HtsReportService } from "@/apps/HTS/services/hts_report_service"
import table from "@/components/DataViews/tables/ReportDataTable"
import { Patientservice } from "@/services/patient_service";
import router from "@/router";
import { modalController } from "@ionic/vue";

export default defineComponent({
    components: { HtsTemplate },
    setup() {
        const report = new HtsReportService('HTS MONTHLY ACTIVITY LOG')
        const params: ReportTemplateParams = {
            logo: 'reports.png',
            title: 'HTS MONTHLY ACTIVITY LOG',
            prefix: 'Clinic',
            columns: [
                [
                    {
                        label: 'Provider ID',
                        value: (d: any) => d.provider_code || 'N/A'
                    },
                    {
                        label: 'Username',
                        value: (d: any) => d.username
                    },
                    {
                        label: 'HIV Rapid',
                        columnType: 'drilldown',
                        value: (d: any) => d.hiv_status
                    },
                    {
                        label: 'Syphilis Rapid',
                        columnType: 'drilldown',
                        value: (d: any) => d.syphilis_status
                    },
                    {
                        label: 'HepB Rapid',
                        columnType: 'drilldown',
                        value: (d: any) => d.hepb_status
                    },
                    {
                        label: 'DBS EID',
                        value: (d: any) => d.dbs_eid  || 'N/A'
                    },
                    {
                        label: 'DBS VL',
                        value: (d: any) => d.dbs_vl || 'N/A'
                    },
                    {
                        label: 'DBS for inconclusives',
                        value: (d: any) => d.dbs_for_inconclusives || 'N/A'
                    },
                    {
                        label: 'Self-Tests distributed',
                        columnType: 'customDrillDown',
                        drillParams: (d: any, reportData: any) => {
                            return {
                                title: "Self tests distributed",
                                columns: [
                                    [
                                        table.thTxt('National ID'),
                                        table.thTxt('First name'),
                                        table.thTxt('Last name'),
                                        table.thTxt('Birthdate'),
                                        table.thTxt('Kits'),
                                        table.thTxt('Action')
                                    ]
                                ],
                                data: d.self_test_kits,
                                generator: async (rows: Array<any>) => {
                                    const row = []
                                    for(const data of rows) {
                                        const id = `${data.person_id}_kits_given`
                                        if (reportData.drill[id]) {
                                            row.push(reportData.drill[id])
                                            continue
                                        }
                                        try {
                                            const patient = new Patientservice((await Patientservice.findByID(data.person_id))) 
                                            reportData.drill[id] = [
                                                table.td(patient.getNationalID()),
                                                table.td(patient.getGivenName()),
                                                table.td(patient.getFamilyName()),
                                                table.tdDate(`${patient.getBirthdate()}`),
                                                table.td(data.self_kits),
                                                table.tdBtn('Show', () => {
                                                router.push({ path: `/patient/dashboard/${id}`})
                                                modalController.dismiss({})
                                                })
                                            ]
                                        row.push(reportData.drill[id])
                                        } catch (e) {
                                            console.error(e)
                                        }
                                    }
                                    return row
                                }
                            }
                        },
                        value: (d: any) => d.self_test_kits,
                        staticValue: (d: any) => d.self_test_kits.reduce((a: number, c: any) => a + c.self_kits, 0)
                    },
                    {
                        label: 'Recency',
                        columnType: 'drilldown',
                        value: (d: any) => d.recency
                    }
                ]
            ],
            hasPeriodSelection: true,
            data: async (r: any, f: any, computedData: any) => {
                report.startDate = computedData.start_date
                report.endDate = computedData.end_date
                return report.requestReport()
            }
        }
        return { params }
    }
})
